.homepage-container {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Montserrat", sans-serif;
    color: #edeeef;
    text-align: center;
    z-index: 2;
}

.home-input-form .query-input {
    padding-right: 60px !important;
}

/* When the .query-input is focused or active, make its placeholder transparent */
.query-input:focus::placeholder,
.query-input:active::placeholder,
.query-input:focus:-ms-input-placeholder,
/* IE 10+ */
.query-input:active:-ms-input-placeholder,
.query-input:focus::-ms-input-placeholder,
/* Edge */
.query-input:active::-ms-input-placeholder,
.query-input:focus::-moz-placeholder,
/* Firefox 19+ */
.query-input:active::-moz-placeholder,
.query-input:focus:-moz-placeholder,
/* Firefox 4â€“18 */
.query-input:active:-moz-placeholder,
.query-input:focus::-webkit-input-placeholder,
/* Chrome/Safari/WebKit */
.query-input:active::-webkit-input-placeholder {
    opacity: 0.3;
    /* or visibility: hidden; */
    transition: opacity 0.2s ease;
    /* optional fade */
}

.home-input-form {
    /* padding-right: 50px; */
    width: min(1140px, 95%);
    height: 60px;
    /* background: #343a40; */
    display: flex;
    border-radius: 20px;
    margin: 0 auto;
    position: relative;
    /* background: linear-gradient(
    135deg,
    rgba(80, 80, 80, 0.6),
    rgba(50, 50, 50, 0.4)
  ); */
    background: linear-gradient(135deg,
            rgba(33, 37, 41, 0.85) 0%,
            rgba(33, 37, 41, 0.65) 50%,
            rgba(33, 37, 41, 0.85) 100%) !important;
    backdrop-filter: blur(6px) !important;
    -webkit-backdrop-filter: blur(10px) !important;
}

/* .home-input-form .query-input {
  flex: 1;
  min-width: 350px;
} */

/* Change the placeholder color for your query input */
.home-input-form .query-input::placeholder {
    color: #edeeef;
    opacity: 0.6;
    font-size: large;
    /* text-align: center; */
}

.home-input-form:focus .input-query:focus .query-input::placeholder,
/* .home-input-form:active .query-input::placeholder, */
.home-input-form.input-query:active .query-input::placeholder {
    opacity: 0;
}

.home-input-form .query-input::-webkit-input-placeholder {
    color: #edeeef;
    opacity: 0.6;
    font-size: large;
}

.home-input-form .query-input::-moz-placeholder {
    color: #edeeef;
    opacity: 0.6;
    font-size: large;
}

.home-input-form .query-input:-ms-input-placeholder {
    color: #edeeed;
    opacity: 0.6;
    font-size: large;
}

.home-input-form .query-input:-moz-placeholder {
    color: #edeeef;
    opacity: 0.6;
    font-size: large;
}

.homepage-bg-wrapper {
    position: absolute;
    /* or fixed if you want it anchored to the viewport */
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* This ensures that scaling won't show outside bounds */
    z-index: 2;
    /* Ensure the background stays behind other content */
}

.homepage-bg {
    position: absolute;
    /* Fixed position for background */
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100vh;
    min-height: 100vh;
    z-index: 1;
    /* Ensure it stays behind all content */
    /* background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.35),
      rgba(0, 0, 0, 0.3),
      rgba(0, 0, 0, 0.25),
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.25),
      rgba(0, 0, 0, 0.1),
      rgba(0, 0, 0, 0.1)
    ),
    url("../../public/home-bg.webp"); */
    /* background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover; Default size */
    scroll-behavior: smooth;
    transition: scale 1ms ease-in-out, filter 0.25ms ease-in-out;
    overflow: hidden;
}

.container {
    width: min(1140px, 95%);
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex: 1 1 100%;
}

.home-div {
    position: relative;
    z-index: 1;
    /* Lower than .space-explorer */
    width: 100%;
    height: calc(100svh - 52px);

    flex-grow: 1;
    flex: 1 1 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

.home-row {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-end;
    margin: 0 auto;
    flex-direction: column;
    flex: 1 1 100%;
    flex-grow: 1;
}

.home-row.info {
    justify-content: space-evenly;
}

/* Wipe Animation Styles */
.profile-image-container {
    position: relative;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 40px rgba(99, 102, 241, 0.3);
    border: 4px solid transparent;
    background: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
    background-clip: padding-box;
    margin-bottom: 2rem;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-image-container:hover {
    transform: scale(1.05);
    box-shadow: 0 0 60px rgba(99, 102, 241, 0.5);
}

.profile-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.profile-img.normal {
    position: absolute;
    top: 0;
    font-weight: bold;
    font-family: "Montserrat", sans-serif;
    color: #edeeef !important;
}

.name:hover {
    cursor: default !important;
}

.changing-text-container em {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.changing-text {
    z-index: 50;
    inset: 0;
    font-size: 1.4rem;
    font-weight: 700;
    margin: 0 auto;
    letter-spacing: 1px;
    color: transparent !important;
    pointer-events: none;
    text-align: center;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(to bottom,
            rgba(237, 238, 239, 1),
            rgba(237, 238, 239, 0.95));
    text-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    font-family: "Montserrat", sans-serif;
    width: 100%;
    white-space: wrap;
    overflow: hidden;
}

.changing-text-animation .TypeAnimation_cursor {
    animation: blink 0.8s steps(2, start) infinite;
}

@keyframes blink {

    0%,
    100% {
        opacity: 0.6;
    }

    50% {
        opacity: 0;
    }
}

@media (max-width: 992px) {
    .name {
        font-size: 4rem;
        font-weight: 500;
    }

    .changing-text {
        font-size: 1.1rem;
        font-weight: 500;
    }
}

@media (max-width: 768px) {
    .name {
        font-size: 2.5rem;
    }

    .changing-text {
        font-size: 1rem;
    }
}

@media (max-width: 576px) {
    .home-input-form .query-input {
        padding-right: 55px !important;
    }

    .name {
        font-size: 2.25rem;
    }

    .home-input-form .query-input::placeholder {
        font-size: medium;
    }

    .home-input-form .query-input::-webkit-input-placeholder {
        font-size: medium;
    }

    .home-input-form .query-input::-moz-placeholder {
        font-size: medium;
    }

    .home-input-form .query-input:-ms-input-placeholder {
        font-size: medium;
    }

    .home-input-form .query-input:-moz-placeholder {
        font-size: medium;
    }
}